//  swiper
.p-swiper__container {
    width: 100%;
}

//  
.p-swiper__pagination-inner {
    position: relative; // paginationabsolute
    width: 100%;
    height: 40px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    //  
    .swiper-horizontal>.swiper-pagination-bullets,
    .swiper-pagination-bullets.swiper-pagination-horizontal,
    .swiper-pagination-custom,
    .swiper-pagination-fraction {
        bottom: 0;
    }
}

//  swiperslider
.swiper {
    /**/
    width: 50%;
    height: 400px;
}

.swiper-slide {
    /**/
    width: 100%;
    height: 100%;
    /**/
    display: flex;
    justify-content: center;
    align-items: center;
    /**/
    color: #fff;
    font-weight: bold;
}


/**/


/**/

.slide1 {
    background-color: #88acbd;
}

.slide2 {
    background-color: #99cb1f;
}

.slide3 {
    background-color: #e43a47;
}