@function flexNumber($minSize,
$maxSize,
$minMq,
$maxMq) {
    $minSizePX: #{$minSize}px;
    $minMqPX: #{$minMq}px;
    @return calc( #{$minSizePX}+((#{$maxSize} - #{$minSize}) * (100vw - #{$minMqPX}) / (#{$maxMq} - #{$minMq})));
}