//    iOS
 :where(html) {
    -webkit-text-size-adjust: none;
}

//  
body {
    overflow-y: auto;
    width: 100%;
    //min-width: 1440px;
    text-align: center;
    overflow-wrap: break-word; //  
    font-family: global.$baseFont; //"Montserrat", "", "Yu Gothic", "", "Yu Gothic Medium", "", sans-serif;
    //font-size: 16px;
    //  https://note.com/skipla/n/ncccb804cb65a rem
    font-size: 62.5%; // -> 10px / 16px > 1.6em
    font-weight: 500; //  
    color: global.$color-font;
    line-height: 1.6875;
    background-color: global.$color-key-white;
    //background-color: global.$color-background;
    //iphone
    -webkit-appearance: none;
    -webkit-text-size-adjust: 100%;
}

#body-css {
    overflow: scroll;
    height: 100px;
    border: 1px solid black;
}

//  IE11
_:lang(x)::-ms-backdrop,
body {
    font-family: "Segoe UI", Meiryo, sans-serif;
}